import React, { useState, useEffect, useMemo } from "react";
import axios from "axios";
import SalesDetail from "./../ui/components/sales/salesDetailComponent";

// Interfaces
interface ProductItem {
  id: string;
  nome: string;
  referencia: string;
  quantidade: number;
  precoUnitario: number;
}

interface Sale {
  codigoVenda: string;
  data: string;
  vendedor: string;
  vendedorId?: string;
  valorTotal: number;
  pagamento: string;
  produtos: ProductItem[];
  desconto: number;
  acrescimo: number;
}

interface ApiResponse<T> {
  success: boolean;
  data: T;
  page: number;
  totalPages: number;
  totalItems: number;
}

interface VendedorOption {
  id: string;
  nome: string;
}

const ITEMS_PER_PAGE = 3;

const SalesList: React.FC = () => {
  const [allSales, setAllSales] = useState<Sale[]>([]);
  const [selectedSale, setSelectedSale] = useState<Sale | null>(null);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [filterDate, setFilterDate] = useState<string>("");
  const [filterVendedor, setFilterVendedor] = useState<string>("");
  const [filterPagamento, setFilterPagamento] = useState<string>("");
  const [currentPage, setCurrentPage] = useState<number>(1);
  const [totalPages, setTotalPages] = useState<number>(1);
  const [loading, setLoading] = useState<boolean>(false);

  // Buscar dados do backend
  useEffect(() => {
    const fetchSales = async () => {
      setLoading(true);
      try {
        const response = await axios.get<ApiResponse<Sale[]>>(
          "http://localhost:3000/venda/loja/1/paginado",
          {
            params: {
              page: currentPage,
              limit: ITEMS_PER_PAGE,
              funcionario_id: filterVendedor || undefined,
              forma_pagamento: filterPagamento || undefined,
              data_inicio: filterDate ? `${filterDate}T00:00:00` : undefined,
              data_fim: filterDate ? `${filterDate}T23:59:59` : undefined,
            },
          }
        );

        if (response.data.success) {
          setAllSales(response.data.data);
          setTotalPages(response.data.totalPages);
        } else {
          console.error("Falha ao carregar vendas:", response.data);
        }
      } catch (error) {
        console.error("Erro ao buscar vendas:", error);
      } finally {
        setLoading(false);
      }
    };

    fetchSales();
  }, [currentPage, filterVendedor, filterPagamento, filterDate]);

  // Paginação
  const handlePageChange = (page: number) => {
    if (page >= 1 && page <= totalPages) {
      setCurrentPage(page);
    }
  };

  // Filtros
  const uniqueVendedores = useMemo(() => {
    const vendedores = new Map<string, string>();
    allSales.forEach((sale) => {
      if (sale.vendedorId && !vendedores.has(sale.vendedorId)) {
        vendedores.set(sale.vendedorId, sale.vendedor);
      } else if (
        !sale.vendedorId &&
        !Array.from(vendedores.values()).includes(sale.vendedor)
      ) {
        vendedores.set(sale.vendedor, sale.vendedor);
      }
    });
    return Array.from(vendedores.entries()).map(([id, nome]) => ({ id, nome }));
  }, [allSales]);

  const uniquePagamentos = useMemo(() => {
    const pagamentos = new Set<string>();
    allSales.forEach((sale) => pagamentos.add(sale.pagamento));
    return Array.from(pagamentos);
  }, [allSales]);

  const openModalWithSale = (sale: Sale) => {
    setSelectedSale(sale);
    setIsModalOpen(true);
  };

  const closeModal = () => {
    setIsModalOpen(false);
    setSelectedSale(null);
  };

  return (
    <div className="quinary p-4 rounded-20px small-shadow">
      <div className="mb-4">
        <h5 className="mb-0 text-white">
          <i className="fas fa-list-ul mr-2"></i>Vendas Registradas
        </h5>
      </div>

      {/* Filtros */}
      <div className="row mb-4">
        <div className="col-md-3">
          <input
            type="date"
            className="form-control input-form"
            value={filterDate}
            onChange={(e) => setFilterDate(e.target.value)}
          />
        </div>
        <div className="col-md-3">
          <select
            className="form-control custom-select input-form"
            value={filterVendedor}
            onChange={(e) => setFilterVendedor(e.target.value)}
          >
            <option value="">Vendedor...</option>
            {uniqueVendedores.map((v: VendedorOption) => (
              <option key={v.id} value={v.id}>
                {v.nome}
              </option>
            ))}
          </select>
        </div>
        <div className="col-md-3">
          <select
            className="form-control custom-select input-form"
            value={filterPagamento}
            onChange={(e) => setFilterPagamento(e.target.value)}
          >
            <option value="">Pagamento...</option>
            {uniquePagamentos.map((p: string) => (
              <option key={p} value={p}>
                {p}
              </option>
            ))}
          </select>
        </div>
        <div className="col-md-3">
          <button
            className="btn primaria btn-block"
            onClick={() => setCurrentPage(1)}
            disabled={loading}
          >
            <i className="fas fa-filter mr-1"></i>Filtrar
          </button>
        </div>
      </div>

      {/* Carregando... */}
      {loading && (
        <div className="text-center text-white">Carregando vendas...</div>
      )}

      {/* Tabela de Vendas */}
      {!loading && (
        <>
          <div className="table-responsive quartenary p-3 rounded-lg mb-4">
            <table className="table table-sm table-borderless text-white">
              <thead>
                <tr className="fine-transparent-border">
                  <th>Cód. Venda</th>
                  <th>Data</th>
                  <th>Vendedor</th>
                  <th>Valor Total</th>
                  <th>Pagamento</th>
                  <th className="text-center">Ações</th>
                </tr>
              </thead>
              <tbody>
                {allSales.length > 0 ? (
                  allSales.map((sale) => (
                    <tr
                      key={sale.codigoVenda}
                      className="fine-transparent-border"
                    >
                      <td>{sale.codigoVenda}</td>
                      <td>{new Date(sale.data).toLocaleString("pt-BR")}</td>
                      <td>{sale.vendedor}</td>
                      <td>R$ {sale.valorTotal.toFixed(2)}</td>
                      <td>{sale.pagamento}</td>
                      <td className="text-center">
                        <div className="btn-group" role="group">
                          <button
                            className="btn btn-sm"
                            style={{
                              backgroundColor: "#17a2b8",
                              color: "white",
                              border: "none",
                              padding: "0.375rem 0.5rem",
                              borderRadius: "50%",
                              width: "32px",
                              height: "32px",
                              marginRight: "4px",
                            }}
                            title="Visualizar"
                            onClick={() => openModalWithSale(sale)}
                          >
                            <i className="fas fa-eye"></i>
                          </button>
                          <button
                            className="btn btn-sm"
                            style={{
                              backgroundColor: "#ffc107",
                              color: "white",
                              border: "none",
                              padding: "0.375rem 0.5rem",
                              borderRadius: "50%",
                              width: "32px",
                              height: "32px",
                              marginRight: "4px",
                            }}
                            title="Editar"
                          >
                            <i className="fas fa-edit"></i>
                          </button>
                          <button
                            className="btn btn-sm"
                            style={{
                              backgroundColor: "#dc3545",
                              color: "white",
                              border: "none",
                              padding: "0.375rem 0.5rem",
                              borderRadius: "50%",
                              width: "32px",
                              height: "32px",
                            }}
                            title="Excluir"
                          >
                            <i className="fas fa-trash"></i>
                          </button>
                        </div>
                      </td>
                    </tr>
                  ))
                ) : (
                  <tr>
                    <td colSpan={6} className="text-center py-4 text-white-75">
                      Nenhuma venda encontrada.
                    </td>
                  </tr>
                )}
              </tbody>
            </table>
          </div>

          {/* Paginação */}
          {totalPages > 1 && (
            <nav aria-label="Navegação de páginas">
              <div className="d-flex justify-content-center align-items-center">
                <button
                  className={`btn primaria px-4 py-2 mx-1 ${
                    currentPage === 1 ? "btn-secondary" : ""
                  }`}
                  onClick={() => handlePageChange(currentPage - 1)}
                  disabled={currentPage === 1}
                >
                  Anterior
                </button>
                {Array.from({ length: totalPages }, (_, i) => i + 1).map(
                  (page) => (
                    <button
                      key={page}
                      className={`btn primaria px-4 py-2 mx-1 ${
                        currentPage === page ? "primaria" : "btn-outline-light"
                      }`}
                      onClick={() => handlePageChange(page)}
                    >
                      {page}
                    </button>
                  )
                )}
                <button
                  className={`btn primaria px-4 py-2 mx-1 ${
                    currentPage === totalPages ? "btn-secondary" : ""
                  }`}
                  onClick={() => handlePageChange(currentPage + 1)}
                  disabled={currentPage === totalPages}
                >
                  Próxima
                </button>
              </div>
            </nav>
          )}
        </>
      )}

      {/* Modal Detalhes da Venda */}
      {selectedSale && (
        <SalesDetail
          show={isModalOpen}
          onClose={closeModal}
          sale={selectedSale}
        />
      )}
    </div>
  );
};

export default SalesList;
